import React, { useState, useRef } from 'react';
import { SketchPicker } from 'react-color';
import SvgComponent from './SvgComponent';
import html2canvas from 'html2canvas';
import './App.css';

const App = () => {
  const [backgroundImage1, setBackgroundImage1] = useState(null);
  const [backgroundImage2, setBackgroundImage2] = useState(null);
  const [gradientColor1, setGradientColor1] = useState('#ffffff');
  const [gradientColor2, setGradientColor2] = useState('#000000');
  const [selectedImage, setSelectedImage] = useState(null); // State to track selected image
  const canvasRef = useRef(null);

  const handleImageUpload = (event, setImage) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImage(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleColorChange1 = (color) => {
    setGradientColor1(color.hex);
  };

  const handleColorChange2 = (color) => {
    setGradientColor2(color.hex);
  };

  const handleImageSelect = (event) => {
    const value = event.target.value;
    // Set selected image based on radio button value
    if (value === 'image1') {
      setSelectedImage(process.env.PUBLIC_URL + '/img/bg1.jpg');
    } else if (value === 'image2') {
      setSelectedImage(process.env.PUBLIC_URL + '/img/bg2.jpg');
    }
    console.log()
  };

  const handleCapture = () => {
    const wrapper = document.querySelector('.wrapper');

    // Use html2canvas to capture .wrapper area
    html2canvas(wrapper).then((canvas) => {
      // Convert canvas content to image data URL
      const image = canvas.toDataURL('image/png');

      // Download the captured image (or handle it as needed)
      const link = document.createElement('a');
      link.download = 'capture.png';
      link.href = image;
      link.click();
    });
  };

  return (
    <div className="App">
      <div className="wrapper" style={{ backgroundImage: `linear-gradient(to bottom, ${gradientColor1}, ${gradientColor2})` }}>
        <div className="wrapper_inner" style={{ backgroundImage: `url(${selectedImage || backgroundImage1})` }}>
          <div className="b_cover_wrap">
            <div className="b_cover">
              {backgroundImage2 && <img src={backgroundImage2} alt="" />}
            </div>
            <SvgComponent />
          </div>
        </div>
      </div>

      <ul className="input_wrap">
        <li>
          Overlay: <input type="file" onChange={(event) => handleImageUpload(event, setBackgroundImage1)} />
        </li>
        <li>
          Cover: <input type="file" onChange={(event) => handleImageUpload(event, setBackgroundImage2)} />
        </li>
        <li>
          Gradient Color 1:
          <SketchPicker color={gradientColor1} onChangeComplete={handleColorChange1} />
        </li>
        <li>
          Gradient Color 2:
          <SketchPicker color={gradientColor2} onChangeComplete={handleColorChange2} />
        </li>
      </ul>
      <ul>
      <li>
          배경 선택
          <label>
            Image 1
            <input type="radio" name="backgroundImage" value="image1" onChange={handleImageSelect} />
          </label>
          <label>
            Image 2
            <input type="radio" name="backgroundImage" value="image2" onChange={handleImageSelect} />
          </label>
        </li>
        <li>
          <button onClick={handleCapture}>Capture .wrapper Area</button>
        </li>
      </ul>
    </div>
  );
};

export default App;
